<template>
  <div id="app">
    test
    <v-chart :options="bar"/>
  </div>
</template>
<script>
let trans=(nam,fun,col)=>({
  type: 'surface',
  name: nam,
  wireframe: {
    // show: false
    lineStyle: {
    //  color: 'white'
    }
  },
  itemStyle: {
    color: col
  },
  equation: {
    x: {
      step: 100,
      min: 0,
      max: 2000
    },
    y: {
      step: 5,
      min: 0,
      max: 100
    },
    z: fun
    }
  })
    // eslint-disable-next-line no-unused-vars
let hei=(attack,p,jg=1.6)=>((x,y)=>{
  let basic=Math.max(attack-x,attack*0.05);
  let pojia=Math.max(attack-x*0.8,attack*0.05);
  let bj_pj=Math.max(attack*1.6-x*0.8,attack*0.05);
  let ret=basic*Math.pow(1-p,4)+pojia*(1-Math.pow(1-p,3))*(1-p)+bj_pj*p;
  return ret/jg;
})
export default {
  name: "App",
  data: function() {
    return {
      bar:{
        legend:{

        },
        tooltip:{},
        backgroundColor: '#fff',
        xAxis3D: {
          type: 'value',
          name: '物防',
          min: 0,
          max: 2000
        },
        yAxis3D: {
          type: 'value',
          name: '法抗',
          min: 0,
          max: 100
        },
        zAxis3D: {
          type: 'value',
          name: '伤害'
        },
        grid3D: {
          viewControl: {
            // projection: 'orthographic'
          }
        },
        series: [trans('黑常态',hei(787*1.08,0.2),'#000000'),
                 trans('黑专三2技能',hei(787*2.38,0.5),'#333333'),
                 trans('黑专三3技能',hei(787*2.88,1,2),'#666666'), 
                 trans('刻俄柏常态理论',(x,y)=>(Math.max((667+0.4*x)*(100-y)/100,667*0.05)/1.6),'#ff0000'),
                 trans('刻俄柏常态实际',(x,y)=>(Math.max(667*(100-y)/100+0.4*x,667*0.05)/1.6),'#ff8888'), 
                 trans('刻俄柏专三2技能理论',(x,y)=>(Math.max((667+0.4*x)*(100-y)/100/(1.6*0.33),667*0.05/(1.6*0.33))),'#ffff00'),
                 trans('刻俄柏专三2技能实际',(x,y)=>(Math.max(((667)*(100-y)/100+0.4*x)/(1.6*0.33),667*0.05/(1.6*0.33))),'#ffff88'), 
                 // eslint-disable-next-line no-unused-vars
                 trans('能天使常态',(x,y)=>(Math.max(549*1.08-x,549*1.08*0.05)*1.12),'#0000ff'),
                 // eslint-disable-next-line no-unused-vars
                 trans('能天使专三3技能',(x,y)=>(Math.max(549*1.08*1.1-x,549*1.08*1.1*0.05)*5*1.12/0.78),'#66ccff'),
                 {
                  type: 'bar3D',
                  name: '普通难度领袖',
                  barSize: [1,1],
                  label:{ show:true,formatter:'{b}'},
                  data:[{
                    name: '弑君者',
                    value: [120,50,5000],
                  },{
                    name: '碎骨',
                    value: [150,30,5000],
                  },{
                    name: '霜星',
                    value: [250,50,5000]
                  },{
                    name: 'W',
                    value: [100,50,5000]
                  },{
                    name: '梅菲斯特',
                    value: [200,60,5000]
                  },{
                    name: '浮士德',
                    value: [350,35,5000]
                  },{
                    name: '冬痕',
                    value: [380,50,5000]
                  },{
                    name: '萨卡兹百夫长',
                    value: [250,35,5000]
                  }
                  ]
                 }
               ]
      }
    }
  }
};
</script>
<style>
#app{
  width: 100%;
  height: 100%;
}
.echarts {
  width: 600px;
  height: 600px;
}
</style>